html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 15px;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

main {
  background-color: #090b1a;
  width: 100vw;
  height: 100vh;
}

.card {
  background-color: #1b1938;
  overflow: hidden;
  width: 1110px;
  height: 446px;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row-reverse;
}
.card .overlay {
  width: 125%;
  background-image: url(../../../images/image-header-desktop.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #aa5cdb;
  background-blend-mode: hard-light;
}
.card-body {
  margin: 75px 120px 0px 70px;
}
.card-title {
  color: white;
  font-family: "Lexend Deca", sans-serif;
  margin-bottom: 30px;
}
.card-title > span {
  color: #aa5cdb;
}
.card-text {
  color: rgba(255, 255, 255, 0.75);
  font-family: "Inter", sans-serif;
  line-height: 1.5rem;
}
.card-stats {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
.card-stats > div {
  margin-top: 75px;
}
.card-stats > div > h2 {
  color: white;
  font-family: "Inter", sans-serif;
  text-transform: uppercase;
  margin-bottom: 0;
}
.card-stats > div > p {
  color: rgba(255, 255, 255, 0.6);
  font-family: "Inter", sans-serif;
  text-transform: uppercase;
  margin-top: 16px;
}

@media (max-width: 39.9375em) {
  .card {
    width: 330px;
    height: 780px;
    flex-direction: column;
  }
  .card .overlay {
    width: 100%;
    height: 100%;
  }
  .card-body {
    margin: 0;
    padding: 34px;
  }
  .card-title, .card-text {
    text-align: center;
  }
  .card-text {
    margin-top: 0;
  }
  .card-stats {
    display: grid;
    grid-template-columns: 1fr;
  }
  .card-stats > div {
    margin-top: 35px;
    margin-left: auto;
    margin-right: auto;
  }
  .card-stats > div > h2, .card-stats p {
    text-align: center;
  }
}